body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.wrapper {
  max-width: 1200px;
  margin: 0 auto; }
  .wrapper .switchLang button {
    padding: 0;
    img-margin: 0; }

.hero {
  max-width: 400px;
  box-shadow: 1px 2px 10px gray;
  padding: 10px;
  color: #2b1705;
  font-family: 'New', Arial, Helvetica, sans-serif;
  font-size: 1.2em; }
  @media screen and (max-width: 600px) {
    .hero {
      min-width: 100%; } }
  .hero img {
    display: block;
    max-width: 380px;
    margin: 0 auto; }
    @media screen and (max-width: 600px) {
      .hero img {
        min-width: 95%; } }

@font-face {
  font-family: "New";
  src: url(/codejam5/static/media/nefont.05be004b.ttf); }

.background {
  padding: 10px 20px;
  text-align: center;
  padding-top: 15vmin;
  background-image: url(/codejam5/static/media/bel-mir.75af3aed.jpg);
  background-size: cover;
  background-repeat: no-repeat; }
  @media screen and (max-width: 640px) {
    .background {
      padding-top: 0;
      background-image: none; } }
  .background h1,
  .background h2 {
    font-family: New, Arial, Helvetica, sans-serif;
    color: #0e0701;
    text-shadow: 5px 5px 20px white; }
  .background h1 {
    font-size: 6vmin; }
  .background h2 {
    font-size: 4vmin; }

.portal {
  display: flex;
  flex-direction: row;
  justify-content: left; }
  @media screen and (max-width: 640px) {
    .portal {
      flex-direction: column; } }

.about-author {
  display: flex;
}

.about-author-text {
  width: 500px;
  text-align: justify;
  margin: 0 auto;
}
.image-gallery-thumbnail-inner .active {
  border: none;
}

.image-gallery {
  display: flex;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
}

.image-gallery-swipe {
  display: flex;
  justify-content: center;
}

.image-gallery-slide img {
  width: auto;
  height: 400px;
  margin: 0 auto;
}

.image-gallery-thumbnail-inner img{
  height: 80px;
}

.youtube-video {
  margin: 0px auto 0;
  padding: 35px;
  background-color: rgb(226, 219, 131);
  padding: 35px;
}

.bigpicture.link.picture img {
  vertical-align: middle;
  border-style: none;
  margin: 0 auto;
}
.authors-container {
  display: flex;
  flex-wrap: wrap;
}

.author-info:hover {
  cursor: pointer;
}

.author-foto {
  width: 195px;
  height: 190px;
}

.image-gallery {
  display: flex;
  margin: 0 auto;
}

.image-gallery-content {
  position: relative;
  width: 100%;
  line-height: 0;
  top: 0;
}

.image-gallery-slide-wrapper {
  position: relative;
  width: 100%;
}
